'use client';

import { motion } from 'framer-motion';
import { Button } from '../common/Button';

export const CTASection = () => {
  // Hardcoded content
  const content = {
    title: "From Minor Chores to Major Opportunities",
    highlightedWords: ["Minor Chores", "Major Opportunities"],
    description: "Empowering the next generation by turning minor chores into major opportunities for personal growth and financial success.",
    primaryCta: {
      text: "Get Started Today",
      link: "#get-started"
    },
    secondaryCta: {
      text: "Learn More",
      link: "#learn-more"
    }
  };

  // Helper function to render title with highlighted words
  const renderTitle = (title: string, highlightedWords: string[] = []) => {
    if (!highlightedWords.length) return title;

    let processedTitle = title;
    highlightedWords.forEach(word => {
      const regex = new RegExp(`\\b${word}\\b`, 'gi');
      processedTitle = processedTitle.replace(regex, `<span class="text-[#2C9B47] font-serif italic">${word}</span>`);
    });

    return processedTitle;
  };

  return (
    <section className="py-20 bg-[#FAFAFA]">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.4 }}
          viewport={{ once: true }}
          className="text-center max-w-4xl mx-auto"
        >
          <h3
            className="text-4xl md:text-5xl font-bold text-gray-900 mb-6 leading-tight tracking-tight"
            dangerouslySetInnerHTML={{
              __html: renderTitle(content.title, content.highlightedWords)
            }}
          />
          <p className="text-xl text-gray-600 mb-8 max-w-3xl mx-auto leading-tight tracking-tight">
            {content.description}
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Button
              size="lg"
              variant="primary"
              onClick={() => window.location.href = content.primaryCta.link}
            >
              {content.primaryCta.text}
            </Button>
            <Button
              size="lg"
              variant="outline"
              onClick={() => window.location.href = content.secondaryCta.link}
            >
              {content.secondaryCta.text}
            </Button>
          </div>
        </motion.div>
      </div>
    </section>
  );
};