'use client';

import { motion } from 'framer-motion';
import LogoLoop from '../common/LogoLoop';

export const TickerSection = () => {
  // Hardcoded content
  const content = {
    title: "Trusted by Families",
    logos: [
      { name: "Local Business 1", link: "#" },
      { name: "Community Center", link: "#" },
      { name: "School District", link: "#" },
      { name: "Neighborhood Association", link: "#" }
    ],
    speed: 60,
    isActive: true
  };

  if (!content.isActive) {
    return null;
  }

  // Sample company logos
  const companyLogos = content.logos.map(logo => ({
    node: (
      <div className="text-2xl font-bold text-white">
        {logo.name}
      </div>
    ),
    title: logo.name,
    href: logo.link
  }));

  return (
    <section className="py-12 bg-[#2C9B47]">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mb-8"
        >
          <h2 className="text-3xl font-bold text-white">
            {content.title}
          </h2>
        </motion.div>
        <LogoLoop
          logos={companyLogos}
          speed={content.speed}
          direction="left"
          logoHeight={48}
          gap={40}
          pauseOnHover
          scaleOnHover
          fadeOut
          fadeOutColor="#2C9B47"
          ariaLabel="Trusted partners"
        />
      </div>
    </section>
  );
};