import { client } from './sanity'
import { landingPageQuery, heroQuery, bannerQuery, valueQuery, howItWorksQuery, testimonialsQuery, moreQuery, tickerQuery, ctaQuery, faqQuery, footerQuery } from './queries'

// Utility function to safely get data with fallbacks
export async function getLandingPageData() {
  try {
    const data = await client.fetch(landingPageQuery)
    return data
  } catch (error) {
    console.warn('No landing page data found in Sanity, using fallback content')
    return null
  }
}

