'use client';

import { motion } from 'framer-motion';
import { Button } from '../common/Button';

export const HeroSection = () => {
  // Hardcoded content
  const content = {
    title: "Kickstart a Neighborhood Business in Minutes",
    highlightedWords: ["Neighborhood", "Minutes"],
    subtitle: "Turn your skills into income. Connect with neighbors who need help with everyday tasks. Start earning today with our simple, secure platform.",
    primaryCta: {
      text: "Get Started Now",
      link: "#get-started"
    },
    secondaryCta: {
      text: "Learn More",
      link: "#learn-more"
    }
  };

  // Helper function to render title with highlighted words
  const renderTitle = (title: string, highlightedWords: string[] = []) => {
    if (!highlightedWords.length) return title;
    
    let processedTitle = title;
    highlightedWords.forEach(word => {
      const regex = new RegExp(`\\b${word}\\b`, 'gi');
      processedTitle = processedTitle.replace(regex, `<span class="text-[#2C9B47] font-serif italic">${word}</span>`);
    });
    
    return processedTitle;
  };

  return (
    <section className="min-h-[100vh] bg-[#FAFAFA] flex items-center px-4 -mt-8">
      <div className="max-w-7xl mx-auto w-full">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
          {/* Left side - Content */}
          <motion.div
            initial={{ opacity: 0, x: -30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            className="text-left"
          >
            <h1 
              className="text-5xl md:text-6xl font-bold text-gray-900 mb-6 leading-none space-y-2"
              dangerouslySetInnerHTML={{ 
                __html: renderTitle(content.title, content.highlightedWords) 
              }}
            />
            
            <motion.p
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.4 }}
              className="text-xl md:text-xl text-gray-600 mb-12 leading-tight tracking-tight"
            >
              {content.subtitle}
            </motion.p>
            
            <motion.div
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.6 }}
              className="flex flex-col sm:flex-row gap-4"
            >
              <Button 
                size="md" 
                variant="primary"
                className="w-full sm:w-auto"
                onClick={() => window.location.href = content.primaryCta.link}
              >
                {content.primaryCta.text}
              </Button>
              <Button 
                size="md" 
                variant="outline"
                className="w-full sm:w-auto"
                onClick={() => window.location.href = content.secondaryCta.link}
              >
                {content.secondaryCta.text}
              </Button>
            </motion.div>
          </motion.div>

          {/* Right side - Image/Video */}
          <motion.div
            initial={{ opacity: 0, x: 30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8, delay: 0.4 }}
            className="flex justify-center lg:justify-end relative"
          >
            {/* Fallback placeholder */}
            <>
              {/* Background layers */}
              <div className="absolute top-4 right-4 w-full max-w-lg h-[32rem] bg-gradient-to-br from-[#2C9B47]/5 to-[#2C9B47]/2 rounded-2xl border border-[#2C9B47]/10" />
              <div className="absolute top-2 right-2 w-full max-w-lg h-[32rem] bg-gradient-to-br from-[#2C9B47]/8 to-[#2C9B47]/4 rounded-2xl border border-[#2C9B47]/15" />
              
              {/* Main container */}
              <div className="relative w-full max-w-lg h-[32rem] bg-gradient-to-br from-[#2C9B47]/10 to-[#2C9B47]/5 rounded-2xl flex items-center justify-center border border-[#2C9B47]/20">
                <div className="text-center">
                  <div className="w-16 h-16 bg-[#2C9B47]/20 rounded-full flex items-center justify-center mx-auto mb-4">
                    <svg className="w-8 h-8 text-[#2C9B47]" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                    </svg>
                  </div>
                  <p className="text-[#2C9B47] font-medium">Image Placeholder</p>
                  <p className="text-[#2C9B47]/60 text-sm mt-1">Hero visual coming soon</p>
                </div>
              </div>
            </>
          </motion.div>
        </div>
      </div>
    </section>
  );
};
